import React from 'react'
import { Link } from 'react-router-dom'

export default function Nav(props) {
  return (
    <div>
         <nav className={`navbar navbar-expand-lg navbar-${props.Mode} bg-${props.Mode}`}>
          
  <div className="container-fluid">
    <a className="navbar-brand fw-bold" href="/">{props.title}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active text-danger" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link text-danger" to="/dlmode">About Me</Link>
        </li>
        <li className="nav-item">
          <a className="nav-link text-danger" href="/">Business</a>
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle text-danger" href="/" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Products
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><a className="dropdown-item" href="/">TDS</a></li>
            <li><a className="dropdown-item" href="/">ERP</a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" href="/">CRM</a></li>
          </ul>
        </li>

      </ul>
      {/*<form className="d-flex" role="search">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-primary" type="submit">Search</button>
      </form>*/}
      <div className={`form-check form-switch text-${props.Mode ==='light'? 'Dark' : 'light'}`}>
    <input className="form-check-input" onClick={props.toggleMode} type="checkbox" id="flexSwitchCheckDefault"/>
    <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable Dark Mode</label>
</div>
    </div>
  </div>
</nav> 
    </div>
  )
}
